body {
    background-color: white;
}

.mobil_container {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow-x: hidden;

    .background {
        position: absolute;

        &.chmury {
            position: fixed;
            width: 100%;
            top: 0;
            z-index: -999;
        }

        &.gory {
            z-index: -997;
            top: 30%;
            left: 50%;
            transform: translateX(-40%);
            height: 70%;
        }
    }

    .headline {
        position: fixed;
        z-index: -998;
        top: 2%;
        width: 80vw;
    }

    .copy {
        position: absolute;
        top: 55%;
        width: 100%;
        display: flex;
        flex-direction: column;

        .arc {
            z-index: 0;
            width: 100%;
        }

        .konkurs_mobile {
            position: absolute;
            width: 90%;
            z-index: 1;
            display: flex;
            flex-direction: column;
            align-items: center;
            top: 16%;
            left: 50%;
            transform: translateX(-50%);
            background-color: white;

            .nagroda {
                width: 100%;
                z-index: 1;
                background-color: white;

                &.blender {
                    width: 100%;
                    height: auto;
                    margin-top: 5%;
                }
            }
        }
    }
}